%ratio {
  display: block;
  position: relative;
}

.ratio {
  &-16-9 {
    @extend %ratio;
    padding-bottom: percentage(calc(9 / 16));
  }
  &-4-3 {
    @extend %ratio;
    padding-bottom: percentage(calc(3 / 4));
  }
  &-1-1 {
    @extend %ratio;
    padding-bottom: 100%;
  }
}
